<template>
    <div>
        <el-table :height="height" highlight-current-row :data="rechargeableCards">
            <el-table-column label="#" prop="index" width="50" fixed="left"></el-table-column>
            <el-table-column label="" prop="name" width="100" align="center" fixed="left"></el-table-column>
            <el-table-column label="" align="center" width="100">
                <template slot-scope="scope">
                    <el-tag size="mini">{{scope.row.category.name}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="()" prop="price">
                <template slot-scope="scope">
                    <span v-if="scope.row.price">{{scope.row.price}}</span>
                    <span v-else></span>
                </template>
            </el-table-column>
            <el-table-column label="()" prop="preferentialPrice"></el-table-column>
            <el-table-column label="()" prop="autoRenewPrice"></el-table-column>
            <el-table-column label="(%)" prop="discount"></el-table-column>
            <!--            <el-table-column label="" prop="cardTypeDesc"></el-table-column>-->
            <el-table-column label="()" prop="amount"></el-table-column>
            <el-table-column label="" prop="typeDesc"></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">{{scope.row.count}}{{scope.row.timeUnit}}</template>
            </el-table-column>
            <el-table-column label="" width="90" align="center">
                <template slot-scope="scope">
                    <el-tag size="mini" v-for="(scenario,index) in scope.row.usageScenariosDesc" :key="index">
                        {{scenario}}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column label="/">
                <template slot-scope="scope">
                    {{scope.row.specifiedStart}}-{{scope.row.specifiedEnd}}
                </template>
            </el-table-column>
            <el-table-column label="" prop="statusDesc" width="90" align="center">
                <template slot-scope="scope">
                    <span v-if="scope.row.status === status.DEFINED_ONLY" class="status-defined_only">{{scope.row.statusDesc}}</span>
                    <span v-else-if="scope.row.status === status.ON" class="status-on">{{scope.row.statusDesc}}</span>
                    <span v-else-if="scope.row.status === status.PREFERENTIAL" class="status-preferential">{{scope.row.statusDesc}}</span>
                    <span v-if="scope.row.status === status.OFF" class="status-off">{{scope.row.statusDesc}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" prop="sort"></el-table-column>
            <el-table-column label="" prop="onSale">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.onSale" @change="onSaleStatusChangeHandler(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="" prop="isRecommend">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.isRecommend"
                               @change="recommendStatusChangeHandler(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="" width="140" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.status < 20" size="mini" type="primary" icon="el-icon-edit"
                               @click="showUpdateDialog(scope.row)"></el-button>
                    <el-button v-if="scope.row.status < 10" size="mini" type="primary"
                               @click="enableRechargeableCardHandler(scope.row.id)">
                    </el-button>
                    <el-button v-else-if="scope.row.status > 10 && scope.row.status < 20" size="mini" type="warning"
                               @click="disableRechargeableCardHandler(scope.row.id)">
                    </el-button>
                    <el-button v-else-if="scope.row.status > 20" size="mini" type="danger" icon="el-icon-delete"
                               @click="showDeleteAlert(scope.row.id)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="" :visible.sync="updateDialogVisible" width="30%">
            <el-form :model="updatingCard" label-width="100px">
                <el-form-item label="">
                    <el-radio-group v-model="updatingCard.is_recommend" class="radio-group">
                        <el-radio :label=true></el-radio>
                        <el-radio :label=false></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-radio-group v-model="updatingCard.on_sale" class="radio-group">
                        <el-radio :label=true></el-radio>
                        <el-radio :label=false></el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="updatingCard.sort" placeholder=" "></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="updateDialogVisible = false"> </el-button>
                <el-button size="small" type="primary" @click="updateHandler"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "Table",
        props: {
            rechargeableCards: {
                default: function () {
                    return []
                },
                type: Array
            },
            fixHeight: {
                default: false,
                type: Boolean
            },
            height: {
                default: 600,
                type: Number
            }
        },
        data() {
            return {
                updateDialogVisible: false,
                updatingCard: {
                    id: null,
                    is_recommend: null,
                    on_sale: null,
                    sort: null,
                },
                status: {
                    DEFINED_ONLY: 0,
                    ON: 11,
                    PREFERENTIAL: 12,
                    OFF: 21
                },
            };
        },
        methods: {
            showUpdateDialog(rechargeableCard) {
                this.updatingCard.id = rechargeableCard.id;
                this.updatingCard.is_recommend = rechargeableCard.isRecommend;
                this.updatingCard.on_sale = rechargeableCard.onSale;
                this.updatingCard.sort = rechargeableCard.sort;
                this.updateDialogVisible = true;
            },
            async updateRechargeableCardCardHandler(card) {
                let rechargeableCard = await this.$command('UPDATE_RECHARGEABLE_CARD', card);
                this.updateDialogVisible = false;
                this.$message.success(``);
                this.$command('DATA_LIST', 'http.rechargeableCards', 'rechargeableCards/setList', this.$requestInput('page'));
            },
            async updateHandler() {
                await this.updateRechargeableCardCardHandler(this.updatingCard);
            },
            // 
            async disableRechargeableCardHandler(rechargeableCardId) {
                this.$confirm('?', '', {
                    type: 'warning',
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async () => {
                    await this.updateRechargeableCardCardHandler({
                        id: rechargeableCardId,
                        status: this.status.OFF
                    });
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                }).catch(() => {
                });
            },
            // 
            async enableRechargeableCardHandler(rechargeableCardId) {
                this.$confirm('?', '', {
                    type: 'warning',
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async () => {
                    await this.updateRechargeableCardCardHandler({
                        id: rechargeableCardId,
                        status: this.status.ON
                    });
                    this.$message({
                        type: 'success',
                        message: ''
                    });
                }).catch(() => {
                });
            },
            // 
            async recommendStatusChangeHandler(rechargeableCard) {
                await this.updateRechargeableCardCardHandler({
                    id: rechargeableCard.id,
                    is_recommend: rechargeableCard.isRecommend
                });
            },
            // 
            async onSaleStatusChangeHandler(rechargeableCard) {
                await this.updateRechargeableCardCardHandler({
                    id: rechargeableCard.id,
                    on_sale: rechargeableCard.onSale
                });
            },
            // 
            async showDeleteAlert(rechargeableCardId) {
                this.$confirm('?', '', {
                    type: 'warning',
                    confirmButtonText: '',
                    cancelButtonText: '',
                }).then(async () => {
                    let response = await this.http.rechargeableCards.delete(rechargeableCardId);
                    this.$message.success(``);
                    this.$command('DATA_LIST', 'http.rechargeableCards', 'rechargeableCards/setList', this.$requestInput('page'));
                }).catch(() => {
                });
            }
        }
    }
</script>

<style scoped>
    .el-radio {
        line-height: 40px !important;
        display: block !important;
        margin-left: 0 !important;
    }

    .radio-group {
        display: flex !important;
    }

    .status-defined_only {
        color: #a3d4b2;
    }

    .status-on {
        color: #50cc73;
    }

    .status-preferential {
        color: #288ad6;
    }

    .status-off {
        color: #9599dc;
    }

</style>

<style>
    .content-box {
        min-height: auto !important;
    }
</style>